.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: var(--primary-color);
  width: 100%;
}

.login-form {
  background-color: white;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

.login-form h2 {
  margin-bottom: 1.5rem;
  color: var(--tertiary-color);
  text-align: center;
}

.input-group {
  margin-bottom: 1rem;
  margin-right: 1rem;
}

.input-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--tertiary-color);
  text-align: left;
}

.input-group input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

.login-button {
  width: 100%;
  padding: 0.75rem;
  background-color: var(--secondary-color);
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.login-button:hover {
  background-color: var(--tertiary-color);
}

.error-message {
  color: #d32f2f;
  text-align: center;
  margin-bottom: 1rem;
}
.page-title {
  font-size: 40px;
}

@media (max-width: 480px) {
  .login-form {
    padding: 1.5rem;
  }
}
